<template>

    <md-card md-with-hover class="bg-white">

        <!--Barra de cima das cards, futuramente podemos explorar essa area para notificacoes-->
        <div class="traco bg-orange"></div>

        <!--Imagem das cards-->
        <md-card-media title="Colocar aqui pop-over para mostrar o que mais pode-se encontrar nesta parte da API">
            <img :src=" image " alt="People"  @click.prevent="toggle()">
        </md-card-media>

        <!--Area dos titulos-->
        <md-card-header >
            <div class="md-title" @click.prevent="toggle()">{{ titulo }} </div>
            <div class="md-subhead"> {{ subtitulo }} </div>
        </md-card-header>

        <!--Area das opcoes de navegacao-->
        <md-card-expand>
            <md-card-actions md-alignment="space-between">
                <div class="bg-orange color-black">

                    <md-button v-if=" link.toString().length > 1" router :to="link">Aceder</md-button>

                </div>

                <md-card-expand-trigger>
                    <md-button class="md-icon-button">
                        <md-icon>keyboard_arrow_down</md-icon>
                    </md-button>
                </md-card-expand-trigger>
            </md-card-actions>

            <md-card-expand-content>
                <md-card-content>
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit. Optio itaque ea, nostrum odio. Dolores, sed accusantium quasi non, voluptas eius illo quas, saepe voluptate pariatur in deleniti minus sint. Excepturi.
                </md-card-content>
            </md-card-expand-content>
        </md-card-expand>

    </md-card>

</template>

<script>
export default {

  props: ['titulo', 'subtitulo', 'image', 'link'],
  methods: {
    toggle () {
      if (this.link === null || this.link === '') {
        this.$emit('snackBar')
      }
    }
  }
}
</script>

<style scoped>
    .md-card {
        margin: 4px;
    }
    @media screen and (min-width: 992px) {
        .md-card {
            width: 24%;
            display: inline-block;
            vertical-align: top;
        }
    }
</style>



