import Vue from 'vue'
import Router from 'vue-router'
import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.css'
import VueProgressBar from 'vue-progressbar'
import Vuetify from 'vuetify'

// Componentes Vue, referenciados nas rotas
import AdminPage from '@/components/admin page/Index'
import Apis from '@/components/apis/Index'
import Console from '@/components/console/Index'
import MainPage from '@/components/main page/Index'

// Usando os componentes externos importados.
Vue.use(Router)
Vue.use(VueMaterial)
Vue.use(Vuetify)
Vue.use(VueProgressBar, {
  color: 'rgb(0, 0, 0)',
  failedColor: 'red',
  height: '2px'
})

export default new Router({
  routes: [
    { path: '/admin',
      name: 'AdminPage',
      component: AdminPage
    },
    { path: '/apis',
      name: 'Apis',
      component: Apis
    },
    { path: '/console',
      name: 'Console',
      component: Console
    },
    { path: '/',
      name: 'MainPage',
      component: MainPage
    }

  ]
})



