<template>
    <section>

        <app-header :title="title"></app-header>

        <div class="card-expansion container" id="api-body">

            <api-option
                    @snackBar="toggleSnackBar()"
                    titulo="Locais"
                    subtitulo="Toda informacao da divisao administrativa de Mocambique"
                    link="/console"
                    image="http://www.mozambique.co.za/images/where-is-mozambique-590x390.jpg">
            </api-option>

            <api-option
                    @snackBar="toggleSnackBar()"
                    titulo="Educacao"
                    link=""
                    subtitulo="Escolas, Universidade,... de Mocambique"
                    image="http://edtechreview.in/images/student-education.jpg">
            </api-option>

            <api-option
                    @snackBar="toggleSnackBar()"
                    titulo="Fotos"
                    link=""
                    subtitulo="Fotos de Mocambique"
                    image="http://intercambistas.com/wp-content/uploads/2017/02/dsc_0139-e1487009838223.jpg">
            </api-option>

            <api-option
                    @snackBar="toggleSnackBar()"
                    titulo="Musicos"
                    link=""
                    subtitulo="Seus artistas favoritos,... de Mocambique"
                    image="https://2.bp.blogspot.com/-h4pqW8tM3-c/WUucmFvvlYI/AAAAAAAAEZI/3M7H2P9SdHY8RAC2Nr1X9ddKgbAUgKe6wCLcBGAs/s640/Mr.Bow%2B-%2BBana%2BXigubo.JPG">
            </api-option>

            <!--Snackbar para a diallog que aparece por baixo da pagina-->
            <md-snackbar class="bg-white" :md-position="position" :md-duration="isInfinity ? Infinity : duration" :md-active.sync="showSnackbar" md-persistent>
                <span class="color-black">API em documentacao...!</span>
                <md-button class="md-primary" @click="showSnackbar = false">Fechar</md-button>
            </md-snackbar>

        </div>

    </section>
</template>

<script>
import apiOption from './API.vue'
import appHeader from '../global components/Header.vue'
import appFooter from '../global components/Footer.vue'
export default {
  name: 'CardExpansion',
  components: {apiOption, appHeader, appFooter},
  data: () => ({
    showSnackbar: false,
    position: 'center',
    duration: 5000,
    title: 'API Collections',
    isInfinity: false
  }),
  beforeCreate () {
    this.$Progress.start()
  },
  mounted () {
    this.$Progress.finish()
  },
  methods: {
    toggleSnackBar () {
      this.showSnackbar = !this.showSnackbar
    }
  }
}
</script>

<style lang="scss" scoped>
    #api-body {
        padding-top: 70px;
    }
</style>



